import config from './WebIMConfig.JS';
import WebIM from 'easemob-websdk';
import vue from '../main'


WebIM.config = config;
WebIM.conn = new WebIM.connection({
    appKey: WebIM.config.appkey,
    isHttpDNS: WebIM.config.isHttpDNS,
    isMultiLoginSessions: WebIM.config.isMultiLoginSessions,
    https: WebIM.config.https,
    url: WebIM.config.socketServer,
    apiUrl: WebIM.config.restServer,
    isAutoLogin: WebIM.config.isAutoLogin,
    autoReconnectNumMax: WebIM.config.autoReconnectNumMax,
    heartBeatWait: WebIM.config.heartBeatWait,
    delivery: WebIM.config.delivery,
    deviceId: WebIM.config.deviceId,
    isDebug: WebIM.config.isDebug,
    useOwnUploadFun: WebIM.config.useOwnUploadFun

})

WebIM.conn.listen({
    onOpened: function () {
        // console.log(" ");
    },                  // 
    onClosed: function () { },                  //
    onTextMessage: function (message) {
        vue.$store.dispatch('onGetCurrentChatObjMsg', message)//
        if (message.type == "chat") {
            vue.$store.dispatch('messageCount', {
                from: message.from,
                text: message.data,
                count: 1
            })
        }
        if (message.type == "groupchat") {
            let obj = {
                text: message.data,
                time: message.time,
                sendPeople: message.from,
            }
            vue.$store.commit("setOtherPeopleTextMessagesArr", obj)
        }
        console.log(message, "");
    },    //
    onEmojiMessage: function (message) {
        console.log(message);
    },   //
    onPictureMessage: function (message) {
        console.log(message);
        vue.$store.dispatch('onGetCurrentChatObjMsg', message)//
    }, //
    onCmdMessage: function (message) {
        console.log(message);
    },     //
    onAudioMessage: function (message) {
        console.log(message);
    },   //
    onLocationMessage: function (message) {
        console.log(message);
    },//
    onFileMessage: function (message) {
        vue.$store.dispatch('onGetCurrentChatObjMsg', message)//
        console.log(message);
    },    //
    onCustomMessage: function (message) { console.log(message); },  //
    onVideoMessage: function (message) {
        var node = document.getElementById('privateVideo');
        var option = {
            url: message.url,
            headers: {
                'Accept': 'audio/mp4'
            },
            onFileDownloadComplete: function (response) {
                var objectURL = WebIM.utils.parseDownloadResponse.call(WebIM.conn, response);
                node.src = objectURL;
            },
            onFileDownloadError: function () {
                console.log('File down load error.')
            }
        };
        WebIM.utils.download.call(WebIM.conn, option);
    },   //
    onPresence: function (message) { console.log(message); },       //-
    onRoster: function (message) { console.log(message); },         //
    onInviteMessage: function (message) { console.log(message); },  //
    onOnline: function () { },                  //
    onOffline: function () { },                 //
    onError: function (message) { console.log(message); },          //
    onBlacklistUpdate: function (list) {       //
        // list
        console.log(list, 'BlacklistUpdate');
    },
    onRecallMessage: function (message) { console.log(message); },      //
    onReceivedMessage: function (message) {
        console.log(message);
    },    //
    onDeliveredMessage: function (message) { console.log(message); },   //
    onReadMessage: function (message) { console.log(message); },        //
    onCreateGroup: function (message) { console.log(message); },        //createGroupNew
    onMutedMessage: function (message) { console.log(message); },       //AA
    onChannelMessage: function (message) { console.log(message); }      //channel ack
});

WebIM.conn.addEventHandler("contactEvent", {
    //  B  A  A 
    onContactInvited: function (msg) {
        console.log(msg, 'onContactInvited');
        // 
        if (msg.from && msg.status) {
            vue.$store.dispatch('updateFriendsApply', {
                id: msg.from,
                text: msg.status
            }),
                vue.$store.commit('hasMesg'),
                vue.$store.commit('addCount')
        }
    },
    //  B  A  A 
    onContactDeleted: function (msg) {
        console.log(msg, 'onContactDeleted');
        if (msg.type === 'unsubscribed') {
            vue.$store.dispatch('delFriendsList', msg.from)
        }
    },
    //  B  A  A  A  B  `onContactAgreed` 
    onContactAdded: function (msg) {
        console.log(msg, 'onContactAdded');
        // 
        if (msg.type === 'subscribed') {
            vue.$store.dispatch('updateFriendsList', {
                id: msg.from,
            })
        }
    },
    //  A  B  B  A 
    onContactRefuse: function (msg) {
        console.log(msg, 'onContactRefuse');
        // 
        if (msg.type === 'unsubscribed') {
            vue.$store.dispatch('refuseFriendsList', msg.from)
        }
    },
    //  A  B  B  A 
    onContactAgreed: function (msg) {
        console.log(msg, 'onContactAgreed');
        // 
        if (msg.type === 'subscribed') {
            vue.$store.dispatch('updateFriendsList', {
                id: msg.from,
            })
            // 
            vue.$store.dispatch('agreeFriendsList', msg.from)
        }
    },

    onGroupEvent: function (msg) {
        // msg.id :id
        // msg.from :
        // msg.operation ,  "memberPresence"
        if (msg.operation == "memberPresence") {
            let obj = {
                id: msg.id,
                isNewCreate: true
            }
            vue.$store.dispatch("setCreateGroupAcceptNews", obj)
        } else if (msg.operation == "updateAnnouncement") {
            // 

            let obj = {
                updateGroupId: msg.id,
                boo: true,
            }
            vue.$store.commit("setIsUpdateGourpAnnouncement", obj)
        } else if (msg.operation == "deleteAnnouncement") {
            // 
        }
    }
});

export default WebIM;