
<template>
  <div id="login">
    <img
      class="close"
      @click="returnRouter"
      src="../../assets/image/images/loginImg/.png"
      alt=""
    />
    <h3></h3>
    <div class="table">
      <div class="area">
        <div>
          <span>/</span>
          <span></span>
        </div>
        <img src="../../assets/image/images/loginImg/more.png" alt="more" />
      </div>
      <p>
        <span>+86</span>
        <span>
          <input type="text" placeholder="" v-model="userId"
        /></span>
      </p>
      <p>
        <span></span>
        <span
          ><input type="password" placeholder="" v-model="password"
        /></span>
      </p>
      <div class="button">
        <button @click="submitLoginInfo"></button>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions, mapState } from "vuex";
import { toast } from "vant";
import WebIM from "../../utils/WebIM";
export default {
  data() {
    return {
      userId: "",
      password: "",
    };
  },
  computed: {
    // Login.js isLogin ,:, :
    ...mapState({
      isLogin: (state) => state.login.isLogin,
    }),
  },
  watch: {
    // ,  '/' 
    isLogin(newV) {
      if (newV) {
        WebIM.conn.updateUserInfo("phone", `${this.userId}`);
        this.$router.push({
          path: "/",
        });
      }
    },
  },
  created() {
    // token
    if (localStorage.getItem("wxUserToken") != undefined) {
      // tonken
     localStorage.removeItem("wxUserToken");
      WebIM.conn.close();
      this.$store.commit("setIsLogin", false);
    }
    // 

    //  
    this.registerRouter();
  },
  methods: {
    // :isByVerification
    ...mapActions(["isByVerification"]),

    //   token
    async submitLoginInfo() {
      let tokenData = {
        grant_type: "password",
        username: this.userId,
        password: this.password,
      };

      await this.$axios
        .post(
          "https://a1.easemob.com/1103220930120764/chat-demo/token",
          JSON.stringify(tokenData)
        )
        .then((response) => {
          let option = {
            userId: this.userId,
            password: this.password,
            token: response.data.access_token,
          };
          //    , 
          this.isByVerification(option);
          toast.success("");
        })
        .catch(function (error) {
          if (error.response.status == 400) {
            toast.fail("/");
          } else if (error.response.status == 404) {
            toast.fail("");
          } else if (error.response.status == 401) {
            toast.fail("[tokentokentoken]");
          } else if (
            error.response.status == 429 ||
            error.response.status == 503 ||
            error.response.status.search(/^5[0.1]{2}$/)
          ) {
            toast.fail("");
          } else {
            toast.fail("appkey or token error");
          }
        });
    },

    // ,,
    registerRouter() {
      if (this.$route.params.isRouterLoginPage) {
        this.userId = this.$route.params.option.userId;
        this.password = this.$route.params.option.password;
      }
    },
    returnRouter() {
      this.$router.go(-1);
    },
  },
};
</script>
<style lang="scss" scoped>
#login {
  width: 100vw;

  .close {
    margin: 15px 10px;
    width: 25px;
  }

  h3 {
    text-align: center;
    margin-top: 50px;
    font-size: 22px;
    margin-top: 20px !important;
    color: #000 !important;
  }

  .table {
    margin-top: 50px;
    width: 100%;
    padding: 0 10px;

    .area {
      padding: 8px 0;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid rgb(220, 218, 218);

      div span:last-child {
        margin-left: 20px;
      }

      img {
        width: 20px;
      }
    }

    p {
      margin: 0;
      display: flex;
      align-items: center;
      justify-content: space-between;

      span {
        margin: 0;
        line-height: 40px;
        padding-right: 50px;
      }

      &:nth-of-type(1) span:nth-child(1) {
        margin-right: 5px;
        border-right: 1px solid rgb(220, 218, 218);
      }

      &:nth-of-type(2) {
        border-top: 1px solid rgb(220, 218, 218);
        border-bottom: 1px solid rgb(220, 218, 218);
      }

      & span:last-child {
        flex: 1;

        input {
          border: 0px;
          outline: 0px;
        }
      }
    }

    .button {
      width: 100%;
      margin-top: 30px;
      text-align: center;
      button {
        width: 90%;
        background-color: var(--theme-freech-green);
        padding: 15px;
        border: 0px solid #000;
        border-radius: 5px;
        color: #fff;
      }
    }
  }
}
</style>