<template>
  <transition>
    <div class="take">
      <nav>
        <div class="left" v-show="show" @click="stopCamera">&lt;</div>
      </nav>

      <div class="demo">
        <div class="box">
          <video
            id="videoCamera"
            class="canvas"
            :width="videoWidth"
            :height="videoHeight"
            autoPlay
          ></video>
          <canvas
            id="canvasCamera"
            class="canvas"
            :width="videoWidth"
            :height="videoHeight"
          ></canvas>
        </div>
        <div class="btn">
          <button class="taking" v-show="show" @click="photographBtn"></button>
        </div>
        <div class="back" @click="resetBtn" v-show="os"></div>
        <button class="finish" @click="submitBtn" v-show="os"></button>
      </div>
    </div>
  </transition>
</template>
<script>
import bus from "@/utils/bus.js";

export default {
  data() {
    return {
      show: true,
      os: false,
      thisVideo: null,
      thisContext: null,
      thisCancas: null,
      imgSrc: "",
      videoWidth: 375,
      videoHeight: 667,
      phone: "",
    };
  },
  created() {
    this.openCamera();
    this.phone = this.$route.query.phone;
  },
  methods: {
    submitBtn() {
      // console.log(this.imgSrc);
      this.$router.push({
        path: "/chatdes",
        query: {
          phone: this.phone,
        },
      });
      bus.$emit("imgurl", this.imgSrc);
    },
    // 
    openCamera() {
      //modelrenderdom,modeldom
      this.$nextTick(() => {
        const _this = this;
        this.os = false; //
        this.thisCancas = document.getElementById("canvasCamera");
        this.thisContext = this.thisCancas.getContext("2d");
        this.thisVideo = document.getElementById("videoCamera");
        // mediaDevices
        if (navigator.mediaDevices === undefined) {
          navigator.mediaDevices = {};
        }
        // mediaDevices
        // getUserMedia
        // getUserMedia
        if (navigator.mediaDevices.getUserMedia === undefined) {
          navigator.mediaDevices.getUserMedia = function (constraints) {
            // getUserMedia()
            let getUserMedia =
              navigator.webkitGetUserMedia ||
              navigator.mozGetUserMedia ||
              navigator.getUserMedia;
            // 
            // 
            if (!getUserMedia) {
              return Promise.reject(
                new Error("getUserMedia is not implemented in this browser")
              );
            }
            // Promisenavigator.getUserMedia
            return new Promise(function (resolve, reject) {
              getUserMedia.call(navigator, constraints, resolve, reject);
            });
          };
        }
        const constraints = {
          audio: false,
          video: {
            width: _this.videoWidth,
            height: _this.videoHeight,
            transform: "scaleX(-1)",
          },
        };
        navigator.mediaDevices
          .getUserMedia(constraints)
          .then(function (stream) {
            // srcObject
            if ("srcObject" in _this.thisVideo) {
              _this.thisVideo.srcObject = stream;
            } else {
              // 
              _this.thisVideo.src = window.URL.createObjectURL(stream);
            }
            _this.thisVideo.onloadedmetadata = function (e) {
              console.log(e);
              _this.thisVideo.play();
            };
          })
          .catch((err) => {
            this.$message({
              message: "",
              err,
              type: "warning",
            });
          });
      });
    },
    //
    photographBtn() {
      this.os = true;
      this.show = false;
      // canvas
      this.thisContext.drawImage(
        this.thisVideo,
        0,
        0,
        this.videoWidth,
        this.videoHeight
      );
      // base64
      this.imgSrc = this.thisCancas.toDataURL("image/png");
    },
    //
    clearCanvas(id) {
      let c = document.getElementById(id);
      let cxt = c.getContext("2d");
      cxt.clearRect(0, 0, c.width, c.height);
    },
    //
    resetBtn() {
      this.os = false;
      this.show = true;
      this.imgSrc = "";
      this.clearCanvas("canvasCamera");
    },
    //
    stopCamera() {
      this.$router.go(-1);
      if (this.thisVideo && this.thisVideo !== null) {
        this.thisVideo.srcObject.getTracks()[0].stop();
        // this.os = true; //
      }
      console.log("!!!");
    },
  },
};
</script>
<style lang="scss" scoped>
.take {
  position: relative;
  width: 100vw;
  height: 100vh;
  background-color: black;

  nav {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    // height: 45px;
    // background-color: black;
    color: white;
    text-align: center;
    line-height: 45px;

    .left {
      float: left;
      margin-left: 10px;
    }
  }

  .demo {
    width: 100%;

    .box {
      width: 100%;

      .videoCamera {
        position: absolute;
        width: 100%;
        height: 100%;
      }

      .canvas {
        position: absolute;
        width: 100%;
      }
    }

    .btn {
      position: absolute;
      bottom: 30px;
      width: 100%;
      display: flex;

      .taking {
        margin: auto;
        width: 70px;
        height: 70px;
        border-radius: 999px;
        border: none;
        box-shadow: 0 0 3px 6px rgba(128, 128, 128, 0.801);
      }
    }
    .back {
      position: absolute;
      top: 10px;
      left: 10px;
      font-size: 30px;
      color: #fff;
    }

    .finish {
      position: absolute;
      bottom: 30px;
      right: 20px;
      width: 60px;
      height: 30px;
      font-size: 14px;
      background-color: rgb(75, 174, 90);
      border-radius: 8px;
      color: #fff;
      border: none;
    }
  }

  .v-enter,
  .v-leave-to {
    transform: translateY(100%);
  }

  .v-enter-active,
  .v-leave-active {
    transition: all 3s linear;
  }

  .v-enter-to,
  .v-leave {
    transform: translateY(0);
  }
}
</style>