<template>
  <div id="register">
    <img
      class="close"
      @click="returnRouter"
      src="../../assets/image/images/loginImg/.png"
      alt=""
    />
    <h3></h3>
    <van-field
      type="number"
      label="/"
      readonly
      placeholder="(+86)"
    />
    <!--  -->
    <van-field
      v-model="userId"
      type="tel"
      label=""
      placeholder=""
    />

    <!--  -->
    <van-field
      v-model="password"
      type="password"
      label=""
      placeholder=""
    />
    <!--  -->
    <van-field
      v-model="rePassword"
      type="password"
      label=""
      placeholder=""
    />
    <p>
      <button
        :class="{
          isCanPress: rePassword !== '' && password !== '' && userId !== '',
        }"
        @click="userRegister"
      >
        
      </button>
    </p>
  </div>
</template>
<script>
import { mapActions, mapState } from "vuex";
import WebIM from "../../utils/WebIM";
// import { toast } from "vant";
// import { compressImage } from "@/assets/js/compressImage";
export default {
  data() {
    return {
      userId: "",
      password: "",
      rePassword: "",
    };
  },
  computed: {
    // register.js isRouterLoginPage ,:(), :/
    ...mapState({
      isRouterLoginPage: (state) => state.register.isRouterLoginPage,
    }),
  },
  watch: {
    // 
    isRouterLoginPage(newV) {
      let option = {
        userId: this.userId,
        password: this.password,
        rePassword: this.rePassword,
      };
      if (newV) {
        this.$router.push({
          name: "Login",
          params: {
            option,
            isRouterLoginPage: this.isRouterLoginPage,
          },
        });
      }
    },
  },
  created() {
    // 
    WebIM.conn.close();
    // tonken
   localStorage.removeItem("wxUserToken");
  },
  methods: {
    // :isRegisterByVerification
    ...mapActions(["isRegisterByVerification"]),
    returnRouter() {
      this.$router.go(-1);
    },
    //  
    userRegister() {
      let option = {
        userId: this.userId,
        password: this.password,
        rePassword: this.rePassword,
      };
      this.isRegisterByVerification(option);
    },
  },
};
</script>
<style lang="scss" scoped>
#register {
  .close {
    margin: 15px 10px;
    width: 25px;
  }
  h3 {
    text-align: center;
    margin-top: 50px;
    font-size: 22px;
    margin-top: 20px !important;
    margin-bottom: 30px !important;
    color: #000 !important;
  }
  .van-field:first-child {
    margin-top: 8px;
  }
  p {
    margin: 25px 0 !important;
    text-align: center;
    button {
      border: 0px solid #000;
      outline: 0px solid #000;
      width: 60%;
      padding: 15px 0;
      border-radius: 5px;
    }

    .isCanPress {
      color: #fff;
      background-color: var(--theme-freech-green);
    }
  }
}
</style>


