// import WebIM from '../utils/WebIM';

const Chat = {
    state: {
        chatRoom: [],
        msgList: [],
        messageCountList: []
    },
    mutations: {
        //
        updateCurrentMsgList(state, messages) {
            // console.log(messages);
            state.msgList.push(messages);
            // console.log(state.msgList);
        },
        // 
        updateMessageCount(state, messages) {
            let isNew = true;
            if (state.messageCountList.length == 0) {
                state.messageCountList.push(messages);
            }
            else {
                state.messageCountList.map(item => {
                    // console.log(item, messages);
                    if (item.from == messages.from) {
                        isNew = false;
                        item.text = messages.text;
                        item.count++;
                    }
                });
                if (isNew) {
                    state.messageCountList.push(messages);
                }
            }
        },
        // 
        cleanThisCount(state, payload) {
            state.messageCountList.map(item => {
                console.log(item.from, payload);
                if (item.from == payload) {
                    item.count = 0;
                }
            })
        }
    },
    actions: {
        //
        onSendText: function (context, payload) {
            console.log(context, payload, "");
            let option = {}
            if (payload.chatType == 'txt') {
                option = {
                    // 
                    type: payload.chatType,
                    // 
                    msg: payload.message,
                    //  ID
                    to: payload.chatId,
                    // 
                    chatType: "singleChat",
                };
            } else {
                option = {
                    // 
                    type: payload.chatType,
                    // 
                    url: payload.url,
                    //  ID
                    to: payload.chatId,
                    // 
                    chatType: "singleChat",
                };
            }

            // 
            let msg = WebIM.message.create(option);
            console.log(msg, "");
            //  `send` 
            WebIM.conn.send(msg).then(() => {
                console.log("Send message success");
                option.to = msg.to;
                context.commit('updateCurrentMsgList', option);
                if (msg.chatType == 'singleChat') {
                    // 
                    context.commit('updateMessageCount', {
                        from: msg.to,
                        text: msg.msg,
                        count: 1
                    })
                }
            }).catch((e) => {
                console.log("Send message fail", e);
            });
        },
        //
        sendImgMessage: function (context, payload) {
            let file = payload.file
            console.log(payload)
            let allowType = {
                jpg: true,
                gif: true,
                png: true,
                bmp: true,
                zip: true,
                txt: true,
                doc: true,
                pdf: true,
            };
            if (file.filetype.toLowerCase() in allowType) {
                let option = {
                    // 
                    type: 'file',//
                    file: file,
                    ext: {
                        // 
                        file_length: file.data.size,
                    },
                    //  ID
                    to: payload.chatId,
                    // 
                    chatType: "singleChat",
                    // 
                    onFileUploadError: function () {
                        console.log("onFileUploadError");
                    },
                    // 
                    onFileUploadProgress: function (e) {
                        console.log(e);
                    },
                    // 
                    onFileUploadComplete: function () {
                        console.log("onFileUploadComplete");
                    },
                };
                // 
                let msg = WebIM.message.create(option);
                //  `send` 
                WebIM.conn.send(msg).then((res) => {
                    option.url = payload.url
                    option.filename = payload.file.filename
                    option.file_length = getSize1(payload.file.data.size)
                    context.commit('updateCurrentMsgList', option)
                    function getSize1(size) {
                        if (size >= 1024 * 1024 * 1024) {
                            size = (size / 1024 / 1024 / 1024).toFixed(2) + 'GB';
                        } else if (size >= 1024 * 1024) {
                            size = (size / 1024 / 1024).toFixed(2) + 'MB';
                        } else if (size >= 1024) {
                            size = (size / 1024).toFixed(2) + 'KB';
                        } else if (size < 1024) {
                            size = size.toFixed(2) + 'bit';
                        }
                        return size;
                    }
                    // 
                    console.log('Success');
                }).catch((e) => {
                    // 
                    console.log("Fail", e);
                });
            }
        },
        //
        onGetCurrentChatObjMsg: function (context, payload) {
            context.commit('updateCurrentMsgList', payload)
        },
        // 
        messageCount(context, payload) {
            context.commit('updateMessageCount', payload)
        }
    }
}

export default Chat;