<template>
  <div class="data-setting">
    <nav>
      <van-row>
        <van-col span="4" @click="backFunc">&lt;</van-col>
        <van-col span="14" offset="1"></van-col>
      </van-row>
    </nav>
    <main>
      <div class="main-center">
        <div class="list between">
          <span></span>
          <span>
            <strong>{{ nickname }}</strong> &gt;</span
          >
        </div>
        <div class="list between">
          <span></span>
          <van-icon name="arrow" color="rgb(107, 107, 107)" />
        </div>
      </div>
      <div class="main-center">
        <div class="list between">
          <span></span>
          <van-icon name="arrow" color="rgb(107, 107, 107)" />
        </div>
        <div class="list between">
          <span></span>
          <van-icon name="arrow" color="rgb(107, 107, 107)" />
        </div>
      </div>
      <div class="main-center">
        <div class="list between">
          <span></span>
          <a-switch v-model="checked1" @change="onChange1" />
        </div>
      </div>
      <div class="main-center">
        <div class="list between">
          <span></span>
          <a-switch v-model="checked2" @change="onChange2" />
        </div>
        <van-popup v-model="isPopup">
          <div class="popup-top">
            <h3></h3>
            <p>
              
            </p>
          </div>
          <div class="popup-bottom">
            <div class="no" @click="cancelFunc()"></div>
            <div class="yes" @click="addBlackFriend(phone)"></div>
          </div>
        </van-popup>
        <div class="list between">
          <span></span>
          <van-icon name="arrow" color="rgb(107, 107, 107)" />
        </div>
      </div>
      <div class="main-bottom">
        <div class="bottom-item" @click="delFriend()"> </div>
      </div>
      <van-popup v-model="isDelFriends">
        <div class="popup-top">
          <h3></h3>
          <p> {{ nickname }} </p>
        </div>
        <div class="popup-bottom">
          <div class="no" @click="cancelDelFriend()"></div>
          <div class="del" @click="confirmDelFriend(phone)"></div>
        </div>
      </van-popup>
    </main>
  </div>
</template>

<script>
import { Toast } from "vant";
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      value: true,
      checked1: false,
      // 
      checked2: false,
      // 
      isPopup: false,
      // 
      isDelFriends: false,
      // 
      length: 0,
    };
  },
  created() {
    this.getBlackFriendsList();
  },
  computed: {
    nickname() {
      return JSON.parse(this.$route.params.list).nickname;
    },
    phone() {
      return JSON.parse(this.$route.params.list).phone;
    },
    ...mapState({
      userId: (state) => state.user.userId,
      blackFriendsBasic: (state) => state.friends.blackFriendsBasic,
    }),
  },
  watch: {
    userId() {
      this.getBlackFriendsList();
    },
    blackFriendsBasic(newVal) {
      this.length = newVal.length;
      this.showIsBlack();
    },
    checked2(newVal) {
      this.checked2 = newVal;
    },
  },
  methods: {
    ...mapActions([
      "delFridends",
      "addBlackFriends",
      "cancelBlackFriends",
      "getBlackFriendsList",
    ]),

    backFunc() {
      this.$router.push({
        name: "home",
      });
    },
    onChange1(checked) {
      console.log(`a-switch to ${checked}`);
    },
    onChange2(checked) {
      console.log(`a-switch to ${checked}`);
      this.isPopup = checked;

      // 
      if (!checked) {
        Toast.success("");
        this.cancelBlackFriends(this.phone);
        setTimeout(() => {
          this.$router.push({
            name: "home",
          });
        }, 1000);
      }
    },
    // true
    showIsBlack() {
      for (let i = 0; i < this.length; i++) {
        if (this.blackFriendsBasic[i].phone === this.phone) {
          this.checked2 = this.blackFriendsBasic[i].isBlack;
        }
      }
    },
    // 
    cancelFunc() {
      this.isPopup = false;
      this.checked2 = false;
    },
    // 
    addBlackFriend(id) {
      this.addBlackFriends(id);
      Toast.success("");

      setTimeout(() => {
        this.$router.push({
          name: "home",
        });
      }, 1000);
    },
    // 
    delFriend() {
      this.isDelFriends = true;
    },
    // 
    confirmDelFriend(id) {
      this.delFridends(id);
      
      Toast.success("");
      this.$router.push({
        name: "home",
      });
    },
    cancelDelFriend() {
      this.isDelFriends = false;
    },
  },
};
</script>

<style lang="scss" scoped>
.data-setting {
  height: 100vh;
  width: 100%;
  overflow: hidden;
  background-color: rgb(17, 17, 17);
  color: white;

  nav {
    width: 100vw;
    height: 45px;
    background-color: rgb(17, 17, 17);
    line-height: 45px;
    color: white;
    text-align: center;
    font-size: 14px;
  }

  .main-center {
    margin-bottom: 10px;
  }

  .main-bottom {
    text-align: center;
    line-height: 40px;

    .bottom-item {
      background-color: rgb(24, 24, 24);
      color: rgb(255, 94, 0);
      border: 1px solid rgba(204, 203, 203, 0.1);
    }
  }

  .list {
    line-height: 25px;
    display: flex;
    flex-direction: row;
    padding: 15px;
    border-bottom: 1px solid rgba(204, 203, 203, 0.1);
    background-color: rgb(24, 24, 24);

    &.between {
      justify-content: space-between;
    }

    strong {
      color: gray;
      margin-right: 5px;
    }
  }

  .ant-switch {
    background-color: rgb(50, 50, 50);
  }

  .ant-switch-checked {
    background-color: rgb(5, 193, 97);
  }

  .van-popup {
    height: 200px;
    width: 85%;
    border-radius: 10px;
    background-color: rgb(35, 35, 35);

    .popup-top {
      padding: 20px;
      line-height: 25px;
      color: whitesmoke;

      h3 {
        text-align: center;
        font-size: 14px;
        margin: 5px 0 10px !important;
      }
    }
    .popup-bottom {
      display: flex;
      flex-direction: row;
      text-align: center;
      margin-top: 18px;
      border-top: 1px solid rgba(204, 203, 203, 0.1);

      div {
        width: 50%;
        height: 50px;
        line-height: 50px;

        &:nth-of-type(1) {
          border-right: 1px solid rgba(204, 203, 203, 0.1);
        }
      }

      .yes {
        color: rgb(160, 196, 247);
      }

      .del {
        color: rgb(255, 94, 0);
      }
    }
  }
}
</style>